/* Scroll to top style */
.scrollToTop {
	@apply bg-indigo-600;
	@apply text-white;
	position: fixed;
	width: 100%;
	align-items: center;
	height: 20px;
	justify-content: center;
	z-index: 999;
	cursor: pointer;
	animation: fadeIn 0.3s;
	transition: opacity 0.4s;
	opacity: 0.6;
}

.scrollToTop:hover {
	opacity: 1;
}

.dark .dark\:scrollToTop {
	@apply bg-indigo-600;
	color: whitesmoke;
	position: fixed;
	width: 100%;
	align-items: center;
	height: 20px;
	justify-content: center;
	z-index: 999;
	cursor: pointer;
	animation: fadeIn 0.3s;
	transition: opacity 0.4s;
	opacity: 0.6;
}
.dark .dark\:scrollToTop:hover {
	opacity: 1;

}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 0.5;
	}
}

.development {
	content:url("../images/develop_light2.svg");
}
.dark .dark\:development {
	content:url("../images/develop_dark3.svg");
}

.pl-5{
	padding-left: 8.5rem;
}
.pr-5{
	padding-right: 8.5rem;
}

.parallax {
	z-index: 999999999;
	/* The image used */
	background-image: url("../images/computer-program-coding-screen.jpg");

	/* Set a specific height */
	min-height: 200px;

	/* Create the parallax scrolling effect */
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.h-10 {
	height: 9.00rem;
}

.h-12 {
	height: 20.00rem;
}

.w-80-per{
	width: 75%;
}

.ribbon {
	z-index: 9999;
	background-color: #1e88e5;
	overflow: hidden;
	white-space: nowrap;
	position: fixed;
	right: -60px;
	bottom: 28px;
	transform: rotate(-45deg);
}
.ribbon a {
	transition: all 1s linear;
	color: #fff;
	display: block;
	font: bold 58% 'Helvetica Neue', Helvetica, Arial, sans-serif;
	margin: 1px 0;
	padding: 5px 50px;
	text-align: center;
	text-decoration: none;
	text-shadow: 0 0 5px #444;
}
